import React from "react";
import { observer } from "mobx-react-lite";

import { Step } from "../../../stores/Game/Round/Step";
import { Game } from "../../../stores/Game";
import { Scores } from "./Scores";
import { ExpectedTricks } from "./ExpectedTricks";

interface IProps {
  game: Game;
}

export const RoundStep = observer(({ game }: IProps) => {
  const {
    currentRound: { step },
    setScores,
    nextRound
  } = game;

  switch (step) {
    case Step.Guess:
      return <ExpectedTricks game={game} />;
    case Step.Play:
      return (
        <button
          className="btn btn-primary btn-block"
          type="button"
          onClick={setScores}
          style={{ margin: "1rem 0" }}
        >
          Scores invoeren
        </button>
      );
    case Step.Score:
      return <Scores game={game} />;
    case Step.End:
      return (
        <button
          className="btn btn-primary btn-block"
          type="button"
          onClick={() => nextRound()}
          style={{ margin: "1rem 0" }}
        >
          Volgende ronde
        </button>
      );
  }
});
