import { observable, action, computed, toJS } from "mobx";

import store from "../index";

import { Player } from "./Player";
import { Round } from "./Round";

import shuffle from "../../utils/shuffle";
import { Step } from "./Round/Step";
import { persist } from "mobx-persist";
import { Route } from "../Router/Route";
import random from "../../utils/random";

const attack = new Audio(`${process.env.PUBLIC_URL}/audio/attack.mp3`);

const yeah = new Audio(`${process.env.PUBLIC_URL}/audio/yeah.mp3`);

export class Game {
  @observable @persist roundNumber: number = 0;
  @observable @persist("list") roundHistory: Round[] = [];
  @observable @persist minTricks: number = 1;
  @observable @persist maxTricks: number = 10;
  @observable @persist amountOfRounds: number = 20;
  @observable @persist("list") players: Player[] = [];
  @observable @persist("list") playerOrder: string[] = [];
  @observable @persist("list") roundTricks: number[] = [];
  @observable @persist loopTricksBack: boolean = true;
  @observable @persist shuffleTricks: boolean = false;
  @observable @persist fullyRandom: boolean = false;
  @observable @persist firstToShuffle: number;

  @action
  setMinTricks = (tricks: number): void => {
    if (tricks < 1 || tricks > this.maxTricks) return;

    this.minTricks = tricks;
  };

  @action
  setMaxTricks = (tricks: number): void => {
    if (tricks < this.minTricks) return;

    this.maxTricks = tricks;
  };

  @action setAmountOfRounds = (rounds: number): void => {
    if (rounds < 1) return;

    this.amountOfRounds = rounds;
  };

  @action
  addPlayer = (name: string): void => {
    const player = new Player(name);
    this.players.push(player);
    this.playerOrder.push(player.id);
  };

  @action
  removePlayer = (id: string): void => {
    this.players = this.players.filter(player => player.id !== id);
    this.playerOrder = this.playerOrder.filter(orderId => orderId !== id);
  };

  @action
  toggleLoopTricksBack = (): void => {
    this.loopTricksBack = !this.loopTricksBack;
  };

  @action
  toggleShuffleTricks = (): void => {
    this.shuffleTricks = !this.shuffleTricks;
  };

  @action
  toggleFullyRandom = (): void => {
    this.fullyRandom = !this.fullyRandom;
  };

  @action
  resetGame = (): void => {
    this.roundNumber = 0;
    this.roundHistory = [];
    this.minTricks = 1;
    this.maxTricks = 10;
    this.players = [];
    this.playerOrder = [];
    this.roundTricks = [];
    this.loopTricksBack = true;
    this.shuffleTricks = false;
    this.fullyRandom = false;

    store.router.updateRoute(Route.New);
  };

  @action
  startGame = (): void => {
    if (this.players.length < 2) return;

    if (this.fullyRandom) {
      for (let i = 0; i < this.amountOfRounds; i += 1) {
        this.roundTricks.push(random(this.minTricks, this.maxTricks));
      }
    } else {
      for (let i = this.minTricks; i <= this.maxTricks; i += 1) {
        this.roundTricks.push(i);
      }

      if (this.loopTricksBack) {
        for (let i = this.maxTricks; i >= this.minTricks; i -= 1) {
          this.roundTricks.push(i);
        }
      }

      if (this.shuffleTricks) {
        this.roundTricks = shuffle(this.roundTricks);
      }
    }

    console.log(toJS(this.roundTricks));

    this.firstToShuffle = random(0, this.players.length - 1);
    this.nextRound();
    store.router.updateRoute(Route.Round);
  };

  @action
  nextRound = (increment: boolean = true): void => {
    if (this.roundNumber >= this.roundTricks.length) {
      yeah.play();
      store.router.updateRoute(Route.Finished);
      return;
    }

    if (increment) this.roundNumber += 1;

    const shuffleOffset: number =
      (this.roundNumber + this.firstToShuffle) % this.players.length;
    const guessOrder: string[] = [];

    for (let i = 0; i < this.playerOrder.length; i += 1) {
      const pointer = (i + shuffleOffset) % this.playerOrder.length;
      guessOrder.push(this.playerOrder[pointer]);
    }

    const round = new Round(
      this.roundNumber,
      guessOrder,
      this.roundTricks[this.roundNumber - 1]
    );

    this.roundHistory.push(round);
  };

  @action
  resetRound = (): void => {
    this.roundHistory.pop();
    this.nextRound(false);
  };

  @computed get currentRound(): Round {
    return this.roundHistory[this.roundHistory.length - 1];
  }

  @action
  guessTricks = (amount: number) => {
    if (
      amount == null ||
      amount < 0 ||
      amount > this.currentRound.amountOfTricks
    ) {
      return;
    }

    // The tricks can't exactly be the totalTricks
    if (this.lastGuess) {
      // last guess of round
      if (this.totalTricks + amount === this.currentRound.amountOfTricks) {
        return;
      }
    }

    this.currentRound.guessedTricks.push({
      playerId: this.currentRound.guessOrder[this.currentRound.guessIndex],
      amount
    });

    this.currentRound.guessIndex++;

    if (this.currentRound.guessIndex === this.currentRound.guessOrder.length) {
      attack.play();
      this.currentRound.step = Step.Play;
    }
  };

  @action
  setScore = (correct: boolean, tricks: number) => {
    let calcTricks;

    const guessedTricks = this.currentRound.guessedTricks[
      this.currentRound.scoreIndex
    ].amount;

    if (correct) {
      calcTricks = guessedTricks;
    } else {
      if (tricks == null) return;
      calcTricks = Math.abs(guessedTricks - tricks);
    }

    this.currentRound.scores.push({
      playerId: this.currentRound.guessOrder[this.currentRound.scoreIndex],
      score: correct ? 10 + calcTricks * 2 : calcTricks * 2 * -1
    });

    this.currentRound.scoreIndex++;

    if (this.currentRound.scoreIndex >= this.currentRound.guessOrder.length) {
      this.currentRound.step = Step.End;
    }
  };

  @action
  setScores = () => {
    this.currentRound.step = Step.Score;
  };

  @computed
  get totalTricks(): number {
    return this.currentRound.guessedTricks.reduce(
      (accumulator, guessedTrick) => accumulator + guessedTrick.amount,
      0
    );
  }

  @computed
  get lastGuess(): boolean {
    return (
      this.currentRound.guessIndex === this.currentRound.guessOrder.length - 1
    );
  }
}
