import React from "react";
import { observer } from "mobx-react-lite";

import { Game } from "../../../stores/Game";

interface IProps {
  game: Game;
}

export const ExpectedTricks = observer(({ game }: IProps) => {
  const {
    players,
    currentRound: { guessIndex, guessOrder, amountOfTricks },
    guessTricks,
    totalTricks,
    lastGuess
  } = game;

  const playerToGuess = players.find(
    player => player.id === guessOrder[guessIndex]
  );

  if (!playerToGuess) return null;

  return (
    <>
      <div className="row">
        <div className="col-sm">
          <p className="text-left">
            Verwachte slagen voor <b>{playerToGuess.name}</b>
          </p>
        </div>
      </div>
      <div className="row">
        <div className="col-sm">
          <div className="input-group mb-3">
            <div className="btn-group btn-block" role="group">
              {[...Array(amountOfTricks + 1)].map((v, i) => {
                if (lastGuess && totalTricks + i === amountOfTricks)
                  return null;
                return (
                  <button
                    type="button"
                    className={`btn btn-lg btn-success`}
                    onClick={() => guessTricks(i)}
                    key={i}
                  >
                    {i}
                  </button>
                );
              })}
            </div>
          </div>
        </div>
      </div>
    </>
  );
});
