import { observable, action } from "mobx";
import { persist } from "mobx-persist";

import { Route } from "./Route";

const soundtrack = new Audio(`${process.env.PUBLIC_URL}/audio/soundtrack.mp3`);
soundtrack.loop = true;

export class Router {
  @observable @persist route: Route = Route.Start;

  @action
  updateRoute = (route: Route) => {
    if (route === Route.New) {
      soundtrack.play();
    } else {
      soundtrack.pause();
    }

    this.route = route;
  };
}
