import React from "react";
import { observer } from "mobx-react-lite";

import { useStores } from "../../../hooks/useStores";
import { RoundStep } from "./RoundStep";
import { ScoreBoard } from "../../ScoreBoard";

export const GameRound = observer(() => {
  const {
    game,
    game: {
      roundNumber,
      players,
      roundHistory,
      resetRound,
      currentRound: { guessOrder, amountOfTricks },
      resetGame
    }
  } = useStores();

  const playerToShuffle = players.find(
    player => player.id === guessOrder[players.length - 1]
  );

  const reset = () => {
    const confirmed: boolean = window.confirm("Herstarten?");

    if (confirmed) {
      resetGame();
    }
  };

  if (!playerToShuffle) return null;

  return (
    <div className="card" style={{ marginTop: "1rem" }}>
      <h5 className="card-header">
        Ronde {roundNumber} - Aantal slagen <b>{amountOfTricks}</b>
      </h5>
      <div className="card-body">
        <div className="row">
          <div className="col">
            <b>{playerToShuffle.name}</b> moet deze ronde schudden
          </div>
        </div>
        <hr />
        <RoundStep game={game} />
        <div className="row">
          <div className="col">
            <ScoreBoard
              rounds={roundHistory}
              players={players}
              reverse={true}
            />
          </div>
        </div>
        <hr />
        <div className="row">
          <div className="col">
            <button
              className="btn btn-danger btn-block"
              type="button"
              onClick={resetRound}
            >
              Herstart ronde
            </button>
          </div>
        </div>
        <button
          style={{
            width: "10rem",
            position: "fixed",
            bottom: "1rem",
            left: "1rem"
          }}
          className="btn btn-danger btn-block"
          type="button"
          onClick={reset}
        >
          Stoppen
        </button>
      </div>
    </div>
  );
});
