import React from "react";
import { observer } from "mobx-react-lite";

import { useStores } from "../../hooks/useStores";
import { ScoreBoard } from "../ScoreBoard";

export const GameFinished = observer(() => {
  const {
    game: { players, roundHistory, resetGame }
  } = useStores();

  return (
    <div className="card" style={{ marginTop: "1rem" }}>
      <h5 className="card-header">Klaar!</h5>
      <div className="card-body">
        <ScoreBoard players={players} rounds={roundHistory} reverse={false} />
        <div className="row">
          <div className="col">
            <button
              className="btn btn-success btn-block"
              type="button"
              onClick={resetGame}
            >
              Nieuw spel
            </button>
          </div>
        </div>
      </div>
    </div>
  );
});
