import React from "react";
import { observer } from "mobx-react-lite";

import { Game } from "../../../stores/Game";

interface IProps {
  game: Game;
}

export const Scores = observer(({ game }: IProps) => {
  const {
    players,
    currentRound: { scoreIndex, guessOrder, guessedTricks, amountOfTricks },
    setScore
  } = game;

  const playerToSetScore = players.find(
    player => player.id === guessOrder[scoreIndex]
  );

  const guessedTrick = guessedTricks.find(
    guessedTrick => guessedTrick.playerId === guessOrder[scoreIndex]
  );

  if (!playerToSetScore || !guessedTrick) return null;

  return (
    <>
      <div className="row" style={{ margin: "1rem 0" }}>
        <div className="col">
          Behaalde score voor: <b>{playerToSetScore.name}</b>. Verwachte slagen:{" "}
          <b>{guessedTrick.amount}</b>
        </div>
      </div>
      <div className="row" style={{ margin: "1rem 0" }}>
        <div className="col">
          <b>Behaalde slagen:</b>
        </div>
      </div>
      <div className="row" style={{ margin: "1rem 0" }}>
        <div className="col-sm">
          <button
            className="btn btn-success btn-lg btn-block"
            type="button"
            onClick={() => setScore(true, 0)}
          >
            Goed
          </button>
        </div>
        <div className="col-sm">
          <div className="btn-group btn-block" role="group">
            {[...Array(amountOfTricks + 1)].map((v, i) => {
              if (guessedTrick.amount === i) return null;

              return (
                <button
                  type="button"
                  className={`btn btn-lg btn-danger`}
                  onClick={() => setScore(false, i)}
                  key={i}
                >
                  {i}
                </button>
              );
            })}
          </div>
        </div>
      </div>
    </>
  );
});
