import React from "react";
import { observer } from "mobx-react-lite";

import { useStores } from "../hooks/useStores";
import { GameNew } from "./pages/GameNew";
import { GameRound } from "./pages/GameRound";
import { GameFinished } from "./pages/GameFinished";
import { Route } from "../stores/Router/Route";
import { GameStart } from "./pages/GameStart";

const App: React.FC = observer(() => {
  const {
    loaded,
    router: { route }
  } = useStores();

  let page;

  if (!loaded) {
    return null;
  }

  switch (route) {
    case Route.Start:
      page = <GameStart />;
      break;
    case Route.New:
      page = <GameNew />;
      break;
    case Route.Round:
      page = <GameRound />;
      break;
    case Route.Finished:
      page = <GameFinished />;
      break;
  }

  return <div className="container">{page}</div>;
});

export default App;
