import { observable } from "mobx";

import { GuessedTricks } from "./GuessedTricks";
import { Score } from "./Score";
import { Step } from "./Step";
import { persist } from "mobx-persist";

export class Round {
  @observable @persist roundNumber: number;
  @observable @persist("list") guessOrder: string[];
  @observable @persist amountOfTricks: number;

  @observable @persist step: Step = Step.Guess;

  @observable @persist guessIndex: number = 0;
  @observable @persist("list") guessedTricks: GuessedTricks[] = [];

  @observable @persist scoreIndex: number = 0;
  @observable @persist scores: Score[] = [];

  constructor(
    roundNumber: number,
    guessOrder: string[],
    amountOfTricks: number
  ) {
    this.roundNumber = roundNumber;
    this.guessOrder = guessOrder;
    this.amountOfTricks = amountOfTricks;
  }
}
