import React from "react";
import { observer } from "mobx-react-lite";

import { useStores } from "../../hooks/useStores";
import { Route } from "../../stores/Router/Route";

export const GameStart = observer(() => {
  const {
    router: { updateRoute }
  } = useStores();

  return (
    <div className="card" style={{ marginTop: "1rem" }}>
      <h5 className="card-header">Boeren Bridge</h5>
      <div className="card-body">
        <div className="row">
          <div className="col">
            <button
              className="btn btn-success btn-block"
              type="button"
              onClick={() => updateRoute(Route.New)}
            >
              Nieuw spel
            </button>
          </div>
        </div>
      </div>
    </div>
  );
});
