import React, { Fragment } from "react";
import { observer } from "mobx-react-lite";

import { Round } from "../stores/Game/Round";
import { Player } from "../stores/Game/Player";

interface IProps {
  players: Player[];
  rounds: Round[];
  reverse: boolean;
}

export const ScoreBoard = observer(({ players, rounds, reverse }: IProps) => {
  return (
    <div className="table-responsive-xl">
      <table className="table table-striped">
        <thead>
          <tr>
            <th scope="col">Ronde</th>
            <th scope="col">Slagen</th>
            {players.map(player => (
              <Fragment key={player.id}>
                <th scope="col">{player.name}</th>
                <th scope="col">Verwacht</th>
              </Fragment>
            ))}
          </tr>
        </thead>
        <tbody>
          <RoundRows rounds={rounds} players={players} reverse={reverse} />
          <TotalRow rounds={rounds} players={players} />
        </tbody>
      </table>
    </div>
  );
});

const RoundRows = observer(({ players, rounds, reverse }: IProps) => {
  return (
    <>
      {(reverse ? [...rounds].reverse() : rounds).map(round => {
        return (
          <RoundRow round={round} players={players} key={round.roundNumber} />
        );
      })}
    </>
  );
});

interface RoundRowProps {
  round: Round;
  players: Player[];
}

const RoundRow = observer(({ round, players }: RoundRowProps) => {
  const tdStyle = { padding: "0.25rem 0.75rem" };

  return (
    <tr key={round.roundNumber}>
      <td style={tdStyle}>{round.roundNumber}</td>
      <td style={tdStyle}>{round.amountOfTricks}</td>
      {players.map(player => {
        const guessedTrick = round.guessedTricks.find(
          guessedTrick => guessedTrick.playerId === player.id
        );

        if (!round.scores.length || round.scores.length !== players.length) {
          return (
            <Fragment key={player.id}>
              <td style={tdStyle}>-</td>
              <td style={tdStyle}>
                {guessedTrick ? guessedTrick.amount : "-"}
              </td>
            </Fragment>
          );
        }

        const score = round.scores.find(score => score.playerId === player.id);

        return (
          <Fragment key={player.id}>
            <td style={tdStyle}>{score?.score}</td>
            <td
              style={{
                ...tdStyle,
                ...(score && score.score < 0 ? { color: "red" } : {})
              }}
            >
              {guessedTrick?.amount}
            </td>
          </Fragment>
        );
      })}
    </tr>
  );
});

interface TotalRowProps {
  rounds: Round[];
  players: Player[];
}

const TotalRow = observer(({ rounds, players }: TotalRowProps) => {
  const totals = new Map();

  rounds.forEach(round => {
    round.scores.forEach(score => {
      if (totals.has(score.playerId)) {
        totals.set(score.playerId, totals.get(score.playerId) + score.score);
      } else {
        totals.set(score.playerId, score.score);
      }
    });
  });

  return (
    <tr>
      <th colSpan={2}>Totaal</th>
      {players.map(player => {
        const score = totals.get(player.id);

        return (
          <td key={player.id} colSpan={2} className="twee">
            {score ? score : "-"}
          </td>
        );
      })}
    </tr>
  );
});
